fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  font-weight: 600;
  margin-bottom: 0.25em;
  padding: 0;
}

label {
  display: inline-block;
  font-weight: 600;
  margin-bottom: 0.25em;
}

input,
select {
  display: block;
  font-family: "Lato";
  font-size: 1em;
}

input,
select[multiple] {
  background-color: white;
  border: 1px solid #eee;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06);
  box-sizing: border-box;
  font-family: "Lato";
  font-size: 1em;
  margin-bottom: 0.5em;
  padding: 0.5em;
  transition: border-color 300ms ease-in-out;
  width: 100%;
}
input:hover,
select[multiple]:hover {
  border-color: shade(#eee, 20%);
}
input:focus,
select[multiple]:focus {
  border-color: #01569f;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.06), 0 0 5px rgba(1, 72, 134, 0.7);
  outline: none;
}
input:disabled,
select[multiple]:disabled {
  background-color: shade(white, 5%);
  cursor: not-allowed;
}
input:disabled:hover,
select[multiple]:disabled:hover {
  border: 1px solid #eee;
}

textarea {
  resize: vertical;
}

[type=search] {
  appearance: none;
}

[type=checkbox],
[type=radio] {
  display: inline;
  margin-right: 0.25em;
}

[type=file] {
  margin-bottom: 0.5em;
  width: 100%;
}

select {
  margin-bottom: 1.5em;
  max-width: 100%;
  width: auto;
}

._form select {
  width: 100%;
}
._form button {
  margin-top: 1em;
}

.site-inner .gform_wrapper {
  margin: 0;
}
.site-inner .gform_wrapper ul {
  margin-left: 0px !important;
}
.site-inner .gform_wrapper ul li {
  list-style-type: none !important;
  padding: 0 !important;
}
.site-inner .gform_wrapper ul li:first-child {
  margin-top: 0;
}
.site-inner .gform_wrapper ul li input,
.site-inner .gform_wrapper ul li select {
  width: 100% !important;
}
.site-inner .gform_wrapper .gform_footer {
  margin: 0;
}

#tinymce ul,
.site-inner ul {
  list-style-type: none;
  margin: 0;
  margin-bottom: 28px;
  padding: 0;
}
#tinymce ul li,
.site-inner ul li {
  list-style-type: none;
  margin-bottom: 0.625rem;
  padding-left: 1.5rem;
  position: relative;
}
#tinymce ul li:before,
.site-inner ul li:before {
  background-color: #555;
  border-radius: 50%;
  content: "";
  height: 8px;
  left: 4px;
  position: absolute;
  top: 8px;
  width: 8px;
}
#tinymce ul li ul,
.site-inner ul li ul {
  margin-bottom: 0;
  margin-top: 0.5em;
}

#tinymce ol,
.site-inner ol {
  margin: 0;
  margin-bottom: 1.5em;
  margin-left: 0;
  padding: 0;
}
#tinymce ol li,
.site-inner ol li {
  margin-bottom: 0.75rem;
  margin-left: 1.5rem;
  position: relative;
}
#tinymce ol li ol,
.site-inner ol li ol {
  margin-bottom: 0;
  margin-top: 0.5em;
}

dl {
  margin-bottom: 1.5em;
}
dl dt {
  margin-top: 0.5em;
}
dl dd {
  margin: 0;
}

dl,
ol,
ul,
p {
  margin-bottom: 1.5em;
}

table {
  border-collapse: collapse;
  margin: 0.5em 0;
  table-layout: fixed;
  width: 100%;
}

th {
  border-bottom: 1px solid shade(#eee, 25%);
  font-weight: 600;
  padding: 0.5em 0;
  text-align: left;
}

td {
  border-bottom: 1px solid #eee;
  padding: 0.5em 0;
}

tr,
td,
th {
  vertical-align: middle;
}

body {
  color: #555;
  font-family: "Lato";
  font-size: 90%;
  line-height: 1.5;
  background-color: white;
}
body *::selection {
  background-color: #01569f;
  color: white;
}
@media screen and (min-width: 900px) {
  body {
    font-size: 1em;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #111;
  font-family: "Lato";
  font-size: 1em;
  font-weight: 600;
  line-height: 1.2;
  margin: 0 0 0.5em;
}

h1,
#heading-one {
  display: block;
  font-family: "Lato";
  font-size: 3.25em;
}

h2,
#post-title {
  font-size: 2.5em;
}

h3 {
  font-size: 2.125em;
}

h4 {
  font-size: 1.75em;
}

h5 {
  font-size: 1.625em;
}

h6 {
  font-size: 1.25em;
}

p {
  margin: 0 0 1.5em;
}

a {
  color: #01569f;
  text-decoration: none;
  transition: all 300ms ease-in-out;
}
a:active, a:focus, a:hover {
  color: #48a0c8;
}

hr {
  border-bottom: 1px solid #eee;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  margin: 1.5em 0;
}

img,
picture {
  margin: 0;
  max-width: 100%;
}

blockquote {
  border-left: 8px solid #48a0c8;
  color: #01569f;
  margin: 4% 5% 4% 0;
  padding: 0 0 0 2rem;
  position: relative;
}
blockquote > *:last-child {
  margin-bottom: 0;
}

code {
  background: #eee;
  border: 1px solid #eee;
  color: #555;
  padding: 2px 4px;
}

.wp-caption .wp-caption-text {
  font-size: 0.75em;
  letter-spacing: 1px;
  line-height: 1.2;
  margin: 10px 0;
  text-transform: uppercase;
}

.base-font {
  font-family: "Lato";
}

.heading-font {
  font-family: "Lato";
}

.uppercase {
  text-transform: uppercase;
}

.normalcase {
  text-transform: none;
}

.lowercase {
  text-transform: lowercase;
}

.small {
  font-size: 0.875em;
}

.large {
  font-size: 1.125em;
}

.light-weight {
  font-weight: 300;
}

.normal-weight,
.font-normal {
  font-weight: 400;
}

.bold-weight,
.font-bold {
  font-weight: 700;
}

.no-bottom-margin {
  display: block;
  margin-bottom: -0.5em;
}

.aligncenter {
  display: block;
  margin: 0 auto 1.5em;
  text-align: center;
}

.alignleft {
  margin: 0 1.5em 1.5em 0;
}

.alignright {
  margin: 0 0 1.5em 1.5em;
}

.text-blue {
  color: #01569f;
}

.text-light-blue {
  color: #48a0c8;
}

.text-dark-gray {
  color: #111;
}

.text-medium-gray {
  color: #555;
}

.text-light-gray {
  color: #eee;
}

.text-white {
  color: white;
}
.text-white a {
  color: white;
  text-decoration: underline;
}
.text-white a:hover {
  text-decoration: none;
}

#tinymce {
  background-color: white;
}

._form button,
input[type=button],
input[type=reset],
input[type=submit],
.blue-button a,
.blue-button button,
.light-blue-button a,
.medium-gray-button a,
.dark-gray-button a,
.white-button a {
  appearance: none;
  background-color: #01569f;
  border: 2px solid transparent;
  color: white !important;
  cursor: pointer;
  display: inline-block;
  font-family: "Lato";
  font-size: 1rem;
  font-weight: 500;
  line-height: 1;
  padding: 1rem 2.75rem;
  text-align: center;
  text-decoration: none;
  transition: all 300ms ease-in-out;
  user-select: none;
  vertical-align: middle;
}
._form button:hover, ._form button:focus,
input[type=button]:hover,
input[type=button]:focus,
input[type=reset]:hover,
input[type=reset]:focus,
input[type=submit]:hover,
input[type=submit]:focus,
.blue-button a:hover,
.blue-button a:focus,
.blue-button button:hover,
.blue-button button:focus,
.light-blue-button a:hover,
.light-blue-button a:focus,
.medium-gray-button a:hover,
.medium-gray-button a:focus,
.dark-gray-button a:hover,
.dark-gray-button a:focus,
.white-button a:hover,
.white-button a:focus {
  background-color: transparent;
  border-color: #01569f;
  color: #01569f !important;
}
._form button:disabled,
input[type=button]:disabled,
input[type=reset]:disabled,
input[type=submit]:disabled,
.blue-button a:disabled,
.blue-button button:disabled,
.light-blue-button a:disabled,
.medium-gray-button a:disabled,
.dark-gray-button a:disabled,
.white-button a:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
._form button:disabled:hover,
input[type=button]:disabled:hover,
input[type=reset]:disabled:hover,
input[type=submit]:disabled:hover,
.blue-button a:disabled:hover,
.blue-button button:disabled:hover,
.light-blue-button a:disabled:hover,
.medium-gray-button a:disabled:hover,
.dark-gray-button a:disabled:hover,
.white-button a:disabled:hover {
  background-color: transparent;
}

.small-button {
  display: inline-block;
}
.small-button a {
  font-size: 0.75rem;
  padding: 0.8125rem 1.5rem;
}

.blue-button a,
.blue-button input[type=submit],
.blue-button button {
  background-color: #01569f;
}
.blue-button a:hover, .blue-button a:focus,
.blue-button input[type=submit]:hover,
.blue-button input[type=submit]:focus,
.blue-button button:hover,
.blue-button button:focus {
  border-color: #01569f;
  color: #01569f !important;
  outline: none;
}

.light-blue-button a,
.light-blue-button input[type=submit],
.light-blue-button button {
  background-color: #48a0c8;
}
.light-blue-button a:hover, .light-blue-button a:focus,
.light-blue-button input[type=submit]:hover,
.light-blue-button input[type=submit]:focus,
.light-blue-button button:hover,
.light-blue-button button:focus {
  border-color: #48a0c8;
  color: #48a0c8 !important;
  outline: none;
}

.medium-gray-button a,
.medium-gray-button input[type=submit],
.medium-gray-button button {
  background-color: #555;
}
.medium-gray-button a:hover, .medium-gray-button a:focus,
.medium-gray-button input[type=submit]:hover,
.medium-gray-button input[type=submit]:focus,
.medium-gray-button button:hover,
.medium-gray-button button:focus {
  border-color: #555;
  color: #555 !important;
  outline: none;
}

.dark-gray-button a,
.dark-gray-button input[type=submit],
.dark-gray-button button {
  background-color: #111;
}
.dark-gray-button a:hover, .dark-gray-button a:focus,
.dark-gray-button input[type=submit]:hover,
.dark-gray-button input[type=submit]:focus,
.dark-gray-button button:hover,
.dark-gray-button button:focus {
  border-color: #111;
  color: #111 !important;
  outline: none;
}

.white-button a,
.white-button input[type=submit],
.white-button button {
  background-color: white;
  color: #111 !important;
}
.white-button a:hover, .white-button a:focus,
.white-button input[type=submit]:hover,
.white-button input[type=submit]:focus,
.white-button button:hover,
.white-button button:focus {
  border-color: white;
  color: white !important;
  outline: none;
}
.white-button a:hover,
.white-button input[type=submit]:hover,
.white-button button:hover {
  background-color: transparent;
}

.blue-button,
.medium-gray-button,
.dark-gray-button,
.white-button {
  display: inline-block;
}